<template>
    <div class="form">
        <input type="text" v-model="poll.title" class="title-input" @blur="validateTitle"/> <br />
        <input type="text" v-model="poll.description" placeholder="Opis formualrza" class="description-input" />

        <div v-for="(question, index) in poll.questions" :key="index" class="question">

            <single-choice-question v-if="question.questionType == 'SingleChoice'"
                :question="question"
                @questionTextLostFocus="validteQuestionText"
                @choiceLostFocus="validateChoice"
                @deleteQuestion="deleteQuestion"
                @deleteChoice="deleteChoice"
                @addChoice="addChoice" />

            <multiple-choice-question v-if="question.questionType == 'MultipleChoice'"
                :question="question"
                @questionTextLostFocus="validteQuestionText"
                @choiceLostFocus="validateChoice"
                @deleteQuestion="deleteQuestion"
                @deleteChoice="deleteChoice"
                @addChoice="addChoice" />

            <text-answer-question v-if="question.questionType == 'TextAnswer'"
                :question="question"
                @questionTextLostFocus="validteQuestionText"
                @deleteQuestion="deleteQuestion" />
        </div>

        <div class="buttons">
            Dodaj pytanie: 
            <a class="btn" @click="addSingleChoiceQuestion">jednokrotny wybór</a> | 
            <a class="btn" @click="addMultipleChoiceQuestion">wielokrotny wybór</a> |
            <a class="btn" @click="addTextAnswerQuestion">odpowiedz tekstowa</a>
        </div>
        

        <div>
            <a class="btn" @click="save">Zapisz</a>
        </div>
    </div>
</template>

<script>
import SingleChoiceQuestion from './SingleChoiceQuestion.vue'
import MultipleChoiceQuestion from './MultipleChoiceQuestion.vue'
import TextAnswerQuestion from './TextAnswerQuestion.vue'

import axios from 'axios'
export default {
    name: 'Poll',
    components: {
        SingleChoiceQuestion,
        MultipleChoiceQuestion,
        TextAnswerQuestion,
    },

    data() {
        return {
            poll: {
                title: 'Formularz bez tytułu',
                description: '',
                questions: [
                    {
                        questionText: 'Tresc pytania',
                        questionType: 'SingleChoice',
                        choices: ['Opcja 1', 'Opcja 2', 'Opcja 3'],
                        number: 1
                    }
                ]
            }
        }
    },

    methods: {

        validteQuestionText(question) {

            // if questionText is emty give it deafult value
            if (question.questionText) {
                return
            }

            question.questionText = "Tresc pytania"
        },

        validateTitle() {
            if(this.poll.title) {
                return
            }

            this.poll.title = "Formularz bez tytułu"
        },

        validateChoice(question, index) {
            if(question.choices[index]) {
                return
            }

            let num = index + 1
            let choiceText = "Opcja " + num

            question.choices.splice(index, 1, choiceText)
        },

        deleteQuestion(question) {
            let index = this.poll.questions.indexOf(question)
            this.poll.questions.splice(index, 1)
        },

        deleteChoice(question, index) {
            question.choices.splice(index, 1)
        },

        addChoice(question) {
            let num = question.choices.length + 1
            question.choices.push("Opcja " + num)
        },

        addSingleChoiceQuestion() {
            this.poll.questions.push({
                    questionText: 'Tresc pytania',
                    questionType: 'SingleChoice',
                    choices: ['Opcja 1', 'Opcja 2', 'Opcja 3'],
                    number: 0
            })
        },

        addMultipleChoiceQuestion() {
            this.poll.questions.push({
                    questionText: 'Tresc pytania',
                    questionType: 'MultipleChoice',
                    choices: ['Opcja 1', 'Opcja 2', 'Opcja 3'],
                    number: 0
            })
        },

        addTextAnswerQuestion() {
            this.poll.questions.push({
                questionText: 'Tresc pytania',
                questionType: 'TextAnswer',
                number: 0
            })
        },

        giveQuestionANumber() {
            let counter = 1
            this.poll.questions.forEach(x => {
                x.number = counter
                counter += 1
            })
        },

        save() {
            this.giveQuestionANumber()

            // prepare request object
            let req = {
                userId: '',
                title: this.poll.title,
                description: this.poll.description,
                singleChoiceQuestions: this.poll.questions.filter(x => x.questionType == 'SingleChoice'),
                multipleChoiceQuestions: this.poll.questions.filter(x => x.questionType == 'MultipleChoice'),
                textAnswerQuestions: this.poll.questions.filter(x => x.questionType == 'TextAnswer')
            }

            axios({
                method: 'post',
                url: 'https://localhost:44392/polls/create',
                data : req
            }).then(res => {
                if(res.status == 200) {
                    window.location.href = 'https://localhost:44392'
                }
            }).catch(() => {
                alert("Coś poszło nie tak przy zapisywaniu formularza, spróbuj ponownie póżniej")
            })
        }
    } 
}
</script>

